import { Component, OnInit } from '@angular/core';

import { MoveInOutService } from '../../../../api/services/move-in-out.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-movein-setup',
  templateUrl: './movein-setup.component.html',
  styleUrls: ['./movein-setup.component.scss']
})
export class MoveinSetupComponent implements OnInit {
  

  isMobile:boolean = false;

  documentDataList:any;

  isDataLoaded:boolean = false;

  documentTypeDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  constructor(
  	private moveInOutService:MoveInOutService,
  	private lookupService:LookupService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }
  
  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getStatus(value){
  	if(value){
  		return 'YES'
  	}
  	else {
  		return 'NO'
  	}
  }

  getDocumentType(priorityId){
    
    var data = _.filter( this.documentTypeDataList, function( item ) {
      if(item.lookupValueId === priorityId)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName == 'General' ? 'low' : 'medium';
    }

  }

  updateDocType(document){


    setTimeout(() => {

      this.isDataLoaded = false;

      var details = {

		"id": parseInt(document.id),
		"description": document.description,
		"documentTypeId": parseInt(document.documentTypeId),
		"owner": document.owner,
		"tenant": document.tenant,
		"isMoveIn": document.isMoveIn,
		"comments": document.comments,
		"isMandate": document.isMandate,
		"isActive": document.isActive,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
		"apartmentId": parseInt(this.cookieService.get('apartmentId'))

      }


      this.moveInOutService.updateMoveInOutConfig(details).subscribe((res:any) => {

        if(res.message){

          this.isDataLoaded = true;
          this.sharedService.setAlertMessage("MoveIn Configuration updated successfully");

        }
        else {
          this.isDataLoaded = true;
        }


      },
      error => {
        this.isDataLoaded = true;
      });

    }, 1000); 

  }

  ngOnInit() {

  	this.moveInOutService.getMoveInOutConfigByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.documentDataList = res.filter(item => {
        return item.isActive;
      });

      this.totalItems = this.documentDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;


    }, error => {
        console.log(error);
    });

    //document type
    this.lookupService.getLookupValueByLookupTypeId(72).subscribe((res:any) => {
          
       this.documentTypeDataList = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );

  }

}
